<template>
    <!--  -->
    <v-list min-width="280" color="amber lighten-5" >
        <!--  -->
        <v-list-item v-if="sortBy">
            <!-- Icon -->
            <v-list-item-avatar :color="sortDesc ? 'error' : 'primary'">
                <v-icon dark>
                    {{ sortDesc ? 'mdi-sort-alphabetical-descending' : 'mdi-sort-alphabetical-ascending' }}
                </v-icon>
            </v-list-item-avatar>
        
            <!--  -->
            <v-list-item-content>
                <v-list-item-title class="font-weight-black">
                    {{ map_header_name[sortBy] }}
                </v-list-item-title>
                <v-list-item-subtitle>{{ sortDesc ? '' : '' }}</v-list-item-subtitle>
            </v-list-item-content>

            <v-spacer></v-spacer>
            
            <!--  -->
            <v-tooltip top color="indigo">
                <template v-slot:activator="{ on, attrs }">
                    <v-btn icon v-bind="attrs" v-on="on"
                        @click="clearSortable">
                        <v-icon>mdi-close-circle</v-icon>
                    </v-btn>
                </template>
                <span></span>
            </v-tooltip>
        </v-list-item>


        <!--  -->
        <v-list-item v-else>
            <!-- Icon -->
            <v-list-item-avatar color="grey">
                <v-icon dark>mdi-format-text-rotation-none</v-icon>
            </v-list-item-avatar>
        
            <!--  -->
            <v-list-item-content>
                <v-list-item-title class="font-weight-black">
                    
                </v-list-item-title>
                <v-list-item-subtitle></v-list-item-subtitle>
            </v-list-item-content>
        </v-list-item>
    </v-list>
</template>

<script>
export default {
    name: "CustomSortDisplay",
    props: {
        sortBy: {
            type: String,
            default: null
        },
        sortDesc: {
            type: Boolean,
            default: false
        },
        headers: {
            type: Array,
            default: () => []
        },
    },
    data(){
        return {

        }
    },
    computed:{
        map_header_name(){ // []:header.value  header.text
            return this.headers.reduce((acc, curV)=> {
                acc[curV['value']] = curV['text'];
                return acc;
            }, {});
        },
    },
    methods: {
        clearSortable(){ //  =>  sortByDisabled
            this.$emit('clearSortable')
        },
    }
}
</script>

<style>

</style>