<template>
    <v-expand-transition class="elevation-10">
            <v-card class="bottom-sheet elevation-10 py-0" v-show="table_selected.length > 0">
                <v-card-title class="font-weight-black text-subtitle-1 py-2"">
                    <div class="d-flex align-center">
                        <span></span>
                        
                        <v-chip color="indigo darken-1" :small="is_mobile"  
                            label outlined class="ml-2 text-caption text-sm-subtitle-2 font-weight-bold">
                            : {{ table_selected.length }}
                        </v-chip>
                    </div>

                    <v-spacer></v-spacer>

                    <!--  -->
                    <div class="d-flex gap-2 align-center flex-wrap" >
                        <!--  -->
                        <v-tooltip top color="error darken-3">
                            <template v-slot:activator="{ on, attrs }">
                                <v-btn 
                                    v-bind="attrs" v-on="on"
                                    color="error darken-1" small @click="deleteAllSelect">
                                    <v-icon small color="white" class="mr-1">mdi-delete</v-icon>
                                    <span class="white--text font-weight-black"></span>
                                </v-btn>
                            </template>
                            <span></span>
                        </v-tooltip>


                        <!--  -->
                        <v-tooltip top color="indigo darken-3">
                            <template v-slot:activator="{ on, attrs }">
                                <v-btn 
                                v-bind="attrs" v-on="on"
                                    color="indigo" small @click="exportBtnSet">
                                    <v-icon small color="white" class="mr-1">mdi-download</v-icon>
                                    <span class="white--text font-weight-black"></span>
                                </v-btn>
                            </template>
                            <span>{{ table_selected.length }}</span>
                        </v-tooltip>

                        
                        <!-- Expand Event() -->
                        <v-tooltip top>
                            <template v-slot:activator="{ on, attrs }">
                                <v-btn class="expand-btn"
                                    v-bind="attrs" v-on="on"
                                    icon @click="is_expand_bottom = !is_expand_bottom">
                                    <v-icon>{{ get_expand_icon() }}</v-icon>
                                </v-btn>
                            </template>
                            <span>{{ is_expand_bottom ? '' : '' }}</span>
                        </v-tooltip>
                    </div>
                </v-card-title>

                <v-divider></v-divider>

                <!--  -->
                <v-card-text class="d-flex black--text py-0">
                    <v-expand-transition>
                        <!--  -->
                        <div class="pa-2 bottom-expand-box" v-show="is_expand_bottom">
                            
                            <!-- (item) -->
                            <!-- <slot 
                                v-for="item in table_selected"
                                name="expand" 
                                :item="item" 
                            /> -->

                            <v-alert 
                                v-for="item in table_selected" 
                                :key="item['id']"
                                color="indigo lighten-5" 
                                elevation="2" 
                                width="100%" 
                                class="mb-0 px-2"
                                >
                                <v-row align="center" class="px-3">
                                    <v-col class="grow">
                                        <!--  -->
                                        <slot name="expand" :item="item"/>
                                    </v-col>
                                
                                    <!--  -->
                                    <v-col class="shrink">
                                        <v-tooltip top>
                                            <template v-slot:activator="{ on, attrs }">
                                                <v-btn v-bind="attrs" v-on="on"
                                                    icon @click="deleteSelect(item)">
                                                    <v-icon>mdi-close-circle</v-icon>
                                                </v-btn>
                                            </template>
                                            <span></span>
                                        </v-tooltip>
                                    </v-col>
                                </v-row>
                            </v-alert>
                            
                           
                        </div>
                    </v-expand-transition>
                </v-card-text>
            </v-card>
        </v-expand-transition>
</template>

<script>
export default {
  name: "CustomTableSelectExpand",
  props: {
    table_selected: {
      // 
      type: Array,
      default: () => [],
    },
  },

  data() {
    return {
      //  / 
      is_expand_bottom: true,
    };
  },

  computed: {},

  methods: {
    // expand Icon
    get_expand_icon() {
      const { is_expand_bottom } = this;
      return is_expand_bottom
        ? "mdi-arrow-down-drop-circle" // 
        : "mdi-arrow-up-drop-circle"; // 
    },

    // 
    exportBtnSet() {
      this.$emit("export"); // Parent Component
    },

    // 
    deleteSelect(select) {
      this.$emit("delete", select); // Parent Component
    },

    // 
    deleteAllSelect() {
      this.$emit("delete-all"); // Parent Component
    },
  },
};
</script>

<style scoped>
/*  */
.bottom-sheet {
  max-width: 400px;
  width: 100%;
  z-index: 10;
  position: fixed;
  bottom: 1.5rem;
}

@media screen and (min-width: 650px) {
  .bottom-sheet {
    right: 1.5rem;
  }
}

@media screen and (max-width: 500px) {
  .bottom-sheet {
    width: 80%;
    bottom: 0;
  }
}

/*  */
.bottom-expand-box {
  height: 150px;
  max-height: 150px;
  width: 100%;
  overflow-y: auto;
  overflow-x: hidden;
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
}
</style>