<template>

  <v-dialog 
    v-model="dialog"
    :width="dialogWidth"
    persistent>

    <v-card>
      <!--  -->
      <v-toolbar dark :color="color">
        <!--  -->
        <v-btn icon dark @click="closeDialog">
          <v-icon>mdi-close-thick</v-icon>
        </v-btn>

        <!--  -->
        <v-toolbar-title>
          {{ title }}
        </v-toolbar-title>
      </v-toolbar>

        <!--  -->
      <v-card-text class="py-4 black--text">

        <slot name="body"></slot>
      </v-card-text>

    </v-card>
  </v-dialog>
</template>

<script>
export default {
  props: {

    value: { // ParentDialog
      type: Boolean,
      default: false,
    },
    color: { // title
        type: String,
        default: "primary",
    },
    title: { // title
        type: String,
        default: ""
    }
  },
  data() {
    return {
      dialog: false,

      originPath: null,
    };
  },

  computed:{
    dialogWidth(){
      const { rwd_name } = this
      const width = {
        'xs': '95%',
        'sm': '80%',
        'md': '70%',
        'lg': '60%',
        'xl': '50%',
      }

      return width[rwd_name]
    }
  },
  
  watch: {
    // Parenttrigger
    value(val) {
      this.dialog = val;

      if(val){ // 
        // Path
        this.originPath = window.location.hash

        // Path #dialog
        history.pushState(
          {dialogOpen: true }, // stateObj
          '', // title
          `${this.originPath}#dialog` // [, url]
        );

        // 
        window.addEventListener('popstate', this.handlePopState);
      }
      else{ // 
        window.removeEventListener('popstate', this.handlePopState);
      }

    },

    //  =>  Parent
    dialog(val) {
      this.$emit('input', val); // 
    }
  },

  mounted() {

    //  => #dialog() => #dialog#dialog
    if (window.location.hash === '#dialog') {
      // 
      this.restoreOriginalHash();
    }
  },

  beforeDestroy() { // 
    window.removeEventListener('popstate', this.handlePopState);
  },

  methods: {
    // Dialog
    closeDialog() {
      this.dialog = false; // 
      history.back(); // URL
    },


    // 
    handlePopState(event) {
      // hash #dialog
      if (window.location.hash === '#dialog') {
        this.dialog = true; //  #hash: Dialog
      } 
      else {
        this.dialog = false;
      }
    },


    // 
    restoreOriginalHash() {
      const { pathname, search } = window.location
      // pathname: /f
      // search: ?record_date=2024-06-06
      const url = pathname + search

      // 
      history.replaceState(null, '', url);
    }
   

  }
};
</script>

