<template>
    <v-list>
        <!--  -->
        <v-list-item disabled>
            <!-- Avatar Icon -->
            <v-list-item-avatar color="grey lighten-3">
                <v-icon>{{ icon }}</v-icon>
            </v-list-item-avatar>

            <!--  -->
            <v-list-item-content>
                <v-list-item-title class="font-weight-black text-subtitle-2">
                    {{ itemTitle }} 
                </v-list-item-title>
                <v-list-item-subtitle class="d-flex flex-wrap" style="gap: 0.5rem;">
                    <v-chip rounded small color="success">: {{ selectedList.length }}</v-chip>
                    <v-chip rounded small color="deep-orange darken-1" class="white--text">: {{ items.length }}</v-chip>
                </v-list-item-subtitle>
            </v-list-item-content>
        </v-list-item>

        <v-divider class="mt-2"></v-divider>

        <!--  -->
        <v-list-item v-if="allowToggle" ripple @mousedown.prevent @click="toggle">
            <v-list-item-action>
                <v-icon :color="items.length > 0 ? 'indigo darken-4' : ''">
                {{ select_All_Icon }}
                </v-icon>
            </v-list-item-action>
            <v-list-item-content>
                <v-list-item-title>
                    
                </v-list-item-title>
            </v-list-item-content>
        </v-list-item>
        
        <!-- () -->
        <v-list-item-group v-model="selectedList" 
            multiple 
            active-class="primary--text"
            class="list-group-container">
            <template v-for="header in items" >
                <!-- disabled: [value, ...] -->
                <v-list-item 
                    :disabled="itemDisabled(header[itemValue])"
                    :value="header[itemValue]"
                    :key="header[itemValue]"
                    @click="change(header)">
                        <template v-slot:default="{ active }">
                            <!-- Checkbox -->
                            <v-list-item-action>
                                <v-checkbox 
                                    :input-value="active" 
                                    :color="getCheckboxColor(header[itemText])">
                                </v-checkbox>
                            </v-list-item-action>
                            
                            <!--  -->
                            <v-list-item-content>
                                <v-list-item-title>
                                    {{ header[itemText] }}
                                    <small 
                                        v-if="itemDisabled(header[itemValue])" 
                                        class="red--text font-weight-black">
                                            (*)
                                    </small>
                                </v-list-item-title>
                            </v-list-item-content>
                            
                            <!-- () -->
                            <v-list-item-action v-if="type === ''">
                                <v-chip class="white--text font-weight-black"
                                    :color="get_type_schedule(header)['color']">
                                    {{ get_type_schedule(header)['text']}}
                                </v-chip>
                            </v-list-item-action>
                        </template>
                </v-list-item>
            </template>
        </v-list-item-group>
    
    </v-list>
</template>

<script>
export default {
    name: "CustomMenuSelectList",
    props: {
        selected: { //  => [<Object1>, <Object2>]
            type: Array,
            default: ()=> []
        },
        items: { // 
            type: Array,
            default: ()=> []
        },
        disabled_select: { //  => [<value1>, ]
            type: Array,
            default: ()=> []
        },

        itemTitle: { // 
            type: String,
            default: '',
        },
        itemValue:{ // value: 
            type: String,
            default: 'value',
        },
        itemText: { // text: 
            type: String,
            default: 'text',
        },

        icon: {
            type: String,
            default: 'mdi-calendar-month',
        },
        iconColor: {
            type: String,
            default: 'grey--text',
        },

        allowToggle: { // 
            type: Boolean,
            default: true
        },

        type:{ 
            type: String,
            default: '', // 
        }

    },
    data(){
        return {
            //  v-list-item-action > v-checkbox
            // !!! Array (value)
            selectedList: [], 
        }
    },
    watch:{
        selected:{
            deep: true,
            handler(newVal){
                this.selectedList = newVal.map(item => item[this.itemValue])
            }
        }
    },
    mounted(){
        this.selectedList = this.selected.map(item => item[this.itemValue])
    },

    computed:{
        selectedDisabledList(){ //  Disabledselect
            const { disabled_select, selected, itemDisabled, itemValue } = this
            //  disabled => 
            if(!disabled_select.length === 0) return []

            //  => filterdisabled header
            return selected.filter(item => itemDisabled(item[itemValue]))
        },
        select_All_Items () { // 
            return this.selectedList.length === this.items.length
        },
        select_Some_Items () { // 
            return this.selectedList.length > 0 && !this.select_All_Items
        },
        select_All_Icon () { // 
            if (this.select_All_Items) return 'mdi-close-box' // icon
            if (this.select_Some_Items) return 'mdi-minus-box' // icon
            return 'mdi-checkbox-blank-outline' // icon
        },
    },

    methods: {
        //  disabled
        itemDisabled(val){
            const {disabled_select} = this
            return disabled_select.includes(val)
        },

        change(select){
            const { itemValue } = this

            if(this.selectedList.find((value) => value === select[itemValue])){
                this.selectedList = this.selectedList.filter((value) => value !== select[itemValue] )
            }else{ //  => 
                this.selectedList.push(select[itemValue])
            }

            //  selectedList: [<value1>, <value2>, ...]
            // Object in Array items + filter
            const return_selects = this.items.filter( (item) => this.selectedList.includes(item[itemValue]) )
            this.$emit("change", return_selects)
        },

        // Toggle
        toggle () {
            this.$nextTick(() => {
                const toggle_selected = this.select_All_Items // 
                                    ? this.selectedDisabledList // (true): disabled
                                    : this.items // (false): 
                
                //  Component [<value1>, <value2>]
                this.selectedList = toggle_selected.map((select) => select[this.itemValue])
                this.$emit("toggle", toggle_selected)
            })
        },


        //  checkbox
        getCheckboxColor(header) {
            return this.itemDisabled(header[this.itemValue]) ? 'grey lighten-1' : 'primary';
        },

        //  type = ''
        get_type_schedule(header){
            const { is_new_pool, status } = header
            if(is_new_pool){ // 
                return {
                    color: 'error',
                    text: '',
                }
            }

            if(status >= 1){ // 
                return {
                    color: 'primary',
                    text: '',
                }
            }
            return { // 
                color: 'grey',
                text: '',
            }
        },

        
    },
}
</script>

<style scoped>
    .list-group-container{
        overflow-y: auto; 
        max-height: 300px;
    }

</style>