import Swal from 'sweetalert2'

// 
export const showDeleteWarning = async (title="", text="") => {
  return await Swal.fire({
    icon: 'warning',
    title: title,
    text: text,
    showCancelButton: true,
    cancelButtonText: '',
    confirmButtonText: '',
  })
}

// 
export const showConfirmDelete = async () => {
  const last_response = await Swal.fire({
    icon: 'warning',
    title: "",
    text: `?`,
    showCancelButton: true,
    cancelButtonText: '',
    confirmButtonText: '',
  })

  if(last_response.isConfirmed){
    return true
  }
  return false
}
