<template>
  <v-container fluid>
    <v-row>
      <!--  -->
      <v-col cols="12">
        <CustomDataTable
            :items="items"
            :itemsLength="itemsLength"
            :options="options"
            :headers="headers"
            :loading="loading"
            :disabled_select="disabled_select"
            :fixed-header="false"
            @emitUpdateOptions="emitUpdateOptions"
            @create="emitCreate"
            show-expand
            >
            <template v-slot:item="{ item, headers, expand, isExpanded }">
              <MenuTableItem
                  :class="{ 'purple lighten-5' : isExpanded}"
                  :item="item" 
                  :headers="headers" 
                  :expand="expand" 
                  :isExpanded="isExpanded"
                  :actions_buttons="actions_buttons"
              />
              
            </template>

            <template v-slot:expanded-item="{ headers, item }">
              <td :colspan="headers.length">
                <MenuRecursiveTable 
                    :items="item['children']"
                    :headers="headers"
                    :loading="loading"
                    hide-default-header
                    :actions_buttons="actions_buttons"
                    :depth="depth + 1">
                </MenuRecursiveTable>
            </td>
            </template>

          
        </CustomDataTable>
      </v-col>
    </v-row>


    <!-- Create -->
    <CustomDialog 
      v-model="create_dialog"
      title=""
      color="pink darken-2">
      <template v-slot:body>
        <CreateMenuForm @refresh="refreshData"/>
      </template>
    </CustomDialog>

    <!-- Read -->
    <CustomDialog 
      v-model="read_dialog"
      title=""
      color="grey darken-2">
      <template v-slot:body>
        <ReadMenuForm 
          :id="read_id"
        />
      </template>
    </CustomDialog>

    <!-- Update -->
    <CustomDialog 
      v-model="update_dialog"
      title=""
      color="primary darken-2">
      <template v-slot:body>
        <UpdateMenuForm 
          :id="update_id"
          @refresh="refreshData"
        />
      </template>
    </CustomDialog>
  </v-container>
</template>

<script>
import { MenuService } from '@/api/services';
import { get_api_pagniation_query_parameter, showConfirmDelete, showDeleteWarning } from "@/utils"
import CustomDialog from "@/components/utils/CustomDialog.vue";
import CustomDataTable from "@/components/utils/CustomDataTable.vue";
import MenuTableItem from '@/components/Admin/Menu/MenuTableItem.vue';
import MenuRecursiveTable from "@/components/Admin/Menu/MenuRecursiveTable.vue"
import ReadMenuForm from '@/components/Admin/Menu/Form/ReadMenuForm.vue';
import CreateMenuForm from '@/components/Admin/Menu/Form/CreateMenuForm.vue';
import UpdateMenuForm from '@/components/Admin/Menu/Form/UpdateMenuForm.vue';
export default {
  components: {
    CustomDialog,
    CustomDataTable,
    MenuTableItem,
    MenuRecursiveTable,
    ReadMenuForm,
    CreateMenuForm,
    UpdateMenuForm,
  },
  data() {
    return {
      dialog: false,
      //#region (API)
      loading: true,
      items: [],
      itemsLength: 0, // 
      //#endregion


       //#region ()
      // 
      disabled_select: ['title'], //  (text)
      
      options: { // 
          page: 1, // 
          itemsPerPage: 30, // : -1: 
          sortBy: ['priority'], // 
          sortDesc: [false], // 
      },
      headers: [ // 
          { text: '', value: 'data-table-expand' },
          { text: '', value: 'title', sortable: true,},
          { text: '', value: 'actions', sortable: true,},
          { text: '', value: 'icon', sortable: true,},
          { text: '', value: 'priority', sortable: true,},
          { text: '', value: 'is_menu', sortable: true,},
          { text: '', value: 'is_disabled', sortable: true,},
          { text: '', value: 'path', sortable: true,},
          { text: '', value: 'name', sortable: true,},
          { text: '', value: 'component', sortable: true,},
          { text: '', value: 'redirect', sortable: true,},
      ],

      //#endregion
    
      //#region (Dialog)
      create_dialog: false,
      read_dialog: false,
      update_dialog: false,
      read_id: null, // ID
      update_id: null, // ID
      //#endregion

      depth: 1,

    };
  },

  computed: {
    actions_buttons(){
        return [
            {
                title: '', 
                icon: 'mdi-eye', icon_color: 'grey darken-1', 
                tooltip_color: 'grey lighten-2', 
                btn_color: 'blue-grey lighten-4', 
                onClick: this.read,
            },
            {
                title: '', 
                icon: 'mdi-pencil', icon_color: 'primary darken-1', 
                tooltip_color: 'blue lighten-4', 
                btn_color: 'primary lighten-4', 
                onClick: this.update,
            },
            {
                title: '', 
                icon: 'mdi-delete', icon_color: 'error darken-1', 
                tooltip_color: 'error lighten-4', 
                btn_color: 'error lighten-4', 
                onClick: this.delete,
            },
        ]
    } 
  },

  async mounted() {
    await this.fetchData()
  },

  methods: {
    //#region (API)
    async fetchData(){
      this.loading = true
      try{
        // Paginationparameter
        const params = get_api_pagniation_query_parameter(this.options)

        // Parent Menu
        params.append("is_children", false)

        // Children
        params.append("include_children", true)
        const response = await MenuService.getAll(params)
        if(response.status === 200){
          const { data, count } = response.data
          this.items = data
          this.itemsLength = count
        }
      }
      catch(err){
        console.error(err);
      }
      finally{
        this.loading = false
      }
    },
    //#endregion

    //#region ($emit)
    // optionAPI
        emitUpdateOptions(options){
            this.options = options
            this.fetchData()
        },
        
        emitCreate(){
          this.create_dialog = true
        },
        read(id){
          this.read_dialog = true
          this.read_id = id
        },
        
        update(id){
          this.update_dialog = true
          this.update_id = id
        },

        async delete(id){
            const first_response = await showDeleteWarning()
            if(first_response.isConfirmed){
              const is_confirm = await showConfirmDelete()
              if(is_confirm){
                try{
                    const response = await MenuService.delete(id)
                    if(response.status === 204){
                        this.$swal.fire('', '', 'success')
                        await this.fetchData() // 
                        await this.$store.dispatch("menu/getMenus")
                    }
                }
                catch(err){
                    this.$swal.fire('', err, 'error')
                }
              }
            }
        },


        async refreshData(){
          // 
          await this.fetchData()
          this.create_dialog = false
          this.update_dialog = false
        },

    //#endregion

  },
};
</script>

<style scoped>
    .expanded-row td:first-child {
        border-left: 10px solid green !important;
    }
</style>